  }
  .footer-contacts__logo-youtube{
    background-image: url(data:image/png;base64,);
    margin-right: 0;
  }
  
  .footer-nav-block{
    display: flex;
    justify-content: center;
    border-top: 1px solid #B3B3B3;  
    border-bottom: 1px solid #B3B3B3;   
    width: 100%;
    height: 362px;
  }
  
  .footer-container-contacts{
    width: 245px;
    padding: 0;
  }
  
  .footer-container{
    width: 180px;
    padding: 0;
  }
  
  .info-container{
    width: 145px;
  }
  
  .footer-menu__logo{
    background-image: url(data:image/png;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    width: 100px;
    height: 56px;
    cursor: pointer;
  }
  
  .footer__list{
    list-style: none;
    margin-bottom: 10px;
  }
  
  .footer__main-text{
    font-size: 18px;
    font-family: "Roboto";
    color: rgb(255, 255, 255);
    line-height: 1.2;
    text-decoration: none;
    cursor: pointer;
    -webkit-transition:.5s;
    transition:.5s;
  }
  
  .footer__second-text{
    font-size: 16px;
    font-family: "Roboto";
    color: #CACACA;
    line-height: 1.2;
    text-decoration: none;
    cursor: pointer;
    -webkit-transition:.5s;
    transition:.5s;
  }
  
  .footer__main-text:hover{text-decoration: none; color: #FE6C00;}
  .footer__second-text:hover{text-decoration: none; color: #FE6C00;}
  .footer__main-text:active{text-decoration: none; color: rgb(255, 255, 255);}
  .footer__second-text:active{text-decoration: none; color: rgb(255, 255, 255);}
  
  .footer__number-text{
    font-size: 29px;
    font-family: "Roboto";
    color: rgb(243, 243, 243);
    line-height: 1.2;
    margin: 0;padding: 0;
  }
  
  .footer__under-text{
    font-size: 13px;
    font-family: "Roboto";
    color: rgb(243, 243, 243);
    line-height: 1.2;
    margin: 0;padding: 0;
