.nav-menu-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.main-menu-container {
  align-self: center;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 54px;
  background-color: #f8f8f8;
  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 1px solid rgb(228, 228, 228);
}

.main-menu {
  align-self: center;
  display: flex;
  justify-content: flex-start;
  height: 53px;
  width: 1170px;
}

.main-menu-list {
  display: flex;
  justify-content: space-between;
  width: 814px;
  margin: 0;
  padding: 0;
}

.menu-list {
  list-style: none;
  height: 51px;
}

.main-menu-list__link {
  display: flex;
  align-items: center;
  font-size: 16px;
  margin: 0;
  padding: 0;
  font-family: "Roboto";
  color: rgb(0, 0, 0);
  line-height: 1.2;
  height: 51px;
  cursor: pointer;
  border-bottom: 2px solid #f8f8f8;
}

.main-menu-list__link:hover {
  text-decoration: none;
  color: rgb(0, 0, 0);
  border-bottom: 2px solid #fe6c00;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.menu-list-add-open {
  position: relative;
  margin: 0;
  padding: 0;
  height: 51px;
  cursor: pointer;
}

.menu-list-add-open:hover .add-menu-container {
  display: block;
}

.add-menu-container {
  display: none;
  position: absolute;
  width: 1170px;
  height: 43px;
  -webkit-animation: fadeIn 0.5s both;
          animation: fadeIn 0.5s both;
}

.add-menu {
  align-self: center;
  width: 1170px;
}

.add-menu-list {
  display: flex;
  justify-content: space-between;
  width: 376px;
  margin: 0;
  padding: 0;
}

.menu-list-add {
  list-style: none;
  height: 43px;
}

.add-menu-list__link {
  display: flex;
  align-items: center;
  font-family: "Roboto";
  font-size: 14px;
  color: rgb(0, 0, 0);
  height: 43px;
}

.add-menu-list__link:hover {
  text-decoration: none;
  cursor: pointer;
}

@-webkit-keyframes fadeIn {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }

@keyframes fadeIn {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
